.contact-section {
    background-color: #f4fafe;
    padding-top: 2rem;
    margin-top: 5rem;
    z-index: 1;
}

.contact-row {
    display: flex;
    padding: 0rem 3rem;
    z-index: 2;
}

.contact-col {
    flex-basis: 50%;
    margin: 10px;
    z-index: 3;
}

.contact-img {
    position: relative;
    width: 100%;
}

.contact-img img {
    position: relative;
    top: 2rem;
    width: 100%;
    object-fit: contain;
    animation: imgMove 7s linear infinite;
}

@keyframes imgMove {

    0%,
    100% {
        transform: translateY(3%);
    }

    60%,
    60% {
        transform: translateY(-3%);
    }

    100%,
    0% {
        transform: translateY(3);
    }

}

.contact-col input {
    width: 30rem;
    height: 55px;
    margin: 10px;
    padding-left: 15px;
    background-color: #f8f9f8;
    border: none;
}

.contact-col input:focus {
    outline: blue solid 1px;
}

.contact-col textarea {
    margin-bottom: 2rem;
    padding-left: 18px;
    background-color: #f8f9f8;
    border: none;
}

.contact-col textarea:focus {
    outline: blue solid 1px;

}

.contact-col textarea::placeholder {
    font-size: 15px;
    padding-top: 8px;
}

.contact-content {
    background-color: white;
    box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.1);
    padding: 2rem 0rem;
    z-index: 4;
    width: 100%;
}

.submiteBtn {
    text-decoration: none;
    color: white;
    background-color: blue;
    padding: 10px 15px;
    border-radius: 5px;
    position: relative;
}

.submiteBtn::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: red;
    top: 0;
    left: 0;
    z-index: -1;
    transform: scaleX(0);
    transform-origin: left;
    transition: 0.5s;
}

.submiteBtn::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: red;
    top: 0;
    left: 0;
    transform: scaleX(0);
    transform-origin: right;
    transition: 0.5s;
}

.submiteBtn:hover::before,
.submiteBtn:hover::after {
    transform: scaleX(1);
    z-index: 1;
}

