.services-pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 10rem;
}

.pagination {
    display: flex;
    align-items: center;
    width: 20rem;
    justify-content: space-around;
}
.pagination.active
{
    background-color: red;
    border-radius: 50px;
}

.pagin {
    background-color: #1c42d8;
    padding: 10px;
    border-radius: 50%;
    width: 1.4rem;
    cursor: pointer;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}

.pagin:first-child,
.pagin:last-child {
    height: 1.3rem;
}