.services-heading3 {
    height: 10rem;
    margin: 8rem 0rem 5rem 0rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    line-height: 30px;
    width: 43rem;
}

.services-heading3 p:first-child {
    color: white;
    font-size: 14px;
    line-height: 0px;
}

.services-heading3 h1 {
    color: white;
    font-size: 35px;
    white-space: pre-wrap;
    line-height: 45px;
}

.services-heading3 p:last-child {
    width: 100%;
    white-space: pre-wrap;
}

.process-section {
    background-color: #051343;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.process-row {
    display: flex;
    padding: 0rem 6rem;
}

.process-col {
    flex-basis: 24%;
    margin: 10px;
    line-height: 25px;
    position: relative;
}

.process-content h3 {
    font-size: 19px;
}

.process-content p {
    font-size: 14px;
    white-space: pre-wrap;
}

.process-img img {
    background-color: rgba(128, 128, 128, 0.206);
    padding: 13px;
    border-radius: 5px;
}

.span img {
    background-color: transparent;
    position: relative;
    left: 4rem;
    bottom: 0;
    animation: moveIcon 5s linear infinite;
}

@keyframes moveIcon {

    0%,
    100% {
        transform: translateX(-5%);
    }

    60%,
    60% {
        transform: translateX(-10%);
    }

    100%,
    0% {
        transform: translateX(30%);
    }
}

.process-img .span {
    position: absolute;
    z-index: 1;
}

.process-img .span::before {
    position: absolute;
    content: "";
    height: 180px;
    border: 1px dashed white;
    transform: rotate(90deg);
    top: -88%;
    left: 190%;
}

.process-img .span4::before {
    display: none;
}


