.blog-row {
    display: flex;
    padding: 0rem 7rem;
}

.blog-col {
    flex-basis: 33%;
    margin: 15px;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    height: 35rem;
    position: relative;
}

.blog-col::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #051242;
    opacity: 0.8;
    bottom: 0;
    left: 0;
    z-index: -1;
    transform: scaleX(0);
    transform-origin: right;
    transition: all 0.5s;
}

.blog-col:hover::after {
    transform: scaleX(1);
}

.blog-col:hover .blog-content,
.blog-col:hover .blog-content h3,
.blog-col:hover .blog-content a,
.blog-col:hover .blog-content p {
    color: white;
}

.blog-col img {
    width: 100%;
}

.blog-content {
    text-align: start;
    margin: 0rem 1rem;
    transition: 0.5s;
}

.blog-col .date {
    display: flex;
    width: 11rem;
    justify-content: space-around;
}

.blog-content p {
    transition: 0.5s;
}

.date p:first-child {
    border-right: 1px solid;
    padding-right: 10px;
}

.date p:last-child {
    color: blue;
}

.blog-content h3 {
    color: #283b5c;
    font-size: 20px;
    transition: 0.5s;
}

.blog-content p:nth-child(3) {
    line-height: 25px;
}

.blog-col a {
    text-decoration: none;
    color: black;
    transition: 0.5s;
    color: rgb(2, 2, 35);
    font-size: 15px;
}

.blog-col a:hover {
    letter-spacing: 1px;
}

