@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&family=Roboto:ital,wght@0,400;1,300&display=swap');

body {
    font-family: 'poppins';
    padding: 0;
    margin: 0;
}

.navbar-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0rem 5rem 0rem 6rem;
    height: 4rem;
    background-color: #f5faff;
    font-size: 14px;
}

.navbar-top-logo {
    color: #314efb;
}

.navbar-top-right {
    display: flex;
    width: 35rem;
    justify-content: space-around;
    align-items: center;
}

.nav-phone i,
.nav-email i {
    margin-right: 0.5rem;
}

.nav-social i {
    font-size: 20px;
    margin: 10px;
    transition: 0.5s;
    cursor: pointer;
}

.nav-social i:hover {
    transform: translateY(-15%);
}

.navbar-bottom {
    display: flex;
    height: 5.5rem;
    align-items: center;
    list-style: none;
    padding: 0rem 3rem 0rem 1rem;
    background-color: #051343;
    color: white;
    justify-content: space-around;
}

.navbar-bottom.sticky {
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 7;
    animation: stick 0.5s linear 1 forwards;
    border-bottom: 1px white;
}

@keyframes stick {
    0% {
        opacity: 0.8;
        transform: translateY(-5%);
    }

    100% {
        opacity: 1;
    }
}

.navbar-icon {
    opacity: 0;
}

.navbar-links {
    display: flex;
    white-space: pre-wrap;
    justify-content: center;
}

.navbar-bottom-right {
    display: flex;
    width: 20rem;
    justify-content: space-around;
}

.navbar-links a {
    cursor: pointer;
    margin: 5px;
    font-size: 17px;
    white-space: pre-wrap;
    position: relative;
    padding: 10px;
}

a:hover .nav-dropdown {
    display: block;
    z-index: 1;
}

.navbar-links a:hover {
    color: dodgerblue;
}

.nav-link {
    text-decoration: none;
    color: white;
}

.nav-dropdown {
    position: absolute;
    z-index: 1;
    left: -10%;
    background-color: white;
    text-align: start;
    padding-left: 0;
    width: 15rem;
    display: none;
    z-index: -1;
}

.nav-dropdown::before {
    content: "";
    position: absolute;
    width: 1rem;
    height: 1rem;
    background-color: white;
    left: 11%;
    transform: rotate(135deg);
    top: -1.7%;
    z-index: -1;
}

.nav-dropdown li {
    list-style-type: none;
    background-color: transparent;
    border-bottom: 1px red;
    position: relative;
}

.nav-dropdown li::before {
    content: "";
    position: absolute;
    background-color: #1c42d8;
    width: 100%;
    height: 50%;
    z-index: -1;
    transform: scaleX(0);
    transform-origin: left;
    transition: 0.4s;
    top: 0;
    left: 0;
}

.nav-dropdown li::after {
    content: "";
    position: absolute;
    background-color: #1c42d8;
    width: 100%;
    height: 50%;
    z-index: -1;
    transform: scaleX(0);
    transform-origin: left;
    transition: 0.4s;
    top: 50%;
    left: 0;
}

.nav-dropdown li:hover::before,
.nav-dropdown li:hover::after {
    transform: scale(1);
}

.nav-dropdown a:hover {
    color: white;
}

.nav-dropdown li:hover a {
    color: white;
}

.nav-dropdown li a {
    margin: 10px;
    line-height: 40px;
    color: black;
}


.navbar-bottom-right input {
    height: 50px;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid white;
    outline: none;
    color: white;
    padding-left: 0.5rem;
    font-size: 17px;
    width: 10rem;
}

.navbar-bottom-right input::placeholder {
    color: white;
}

.navbar-bottom-right button {
    width: 8rem;
    border-radius: 5px;
    cursor: pointer;
    border: none;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    z-index: 5;
    font-weight: bold;
    overflow: hidden;
    background-color: #1c42d8;
    transition: 0.5s;
}

button::before {
    content: "";
    position: absolute;
    background-color: white;
    height: 50%;
    width: 100%;
    z-index: -1;
    top: 0;
    left: 0;
    transition: transform 0.5s;
    transform: scaleX(0);
    transform-origin: right;
}

button::after {
    content: "";
    position: absolute;
    background-color: white;
    height: 50%;
    width: 100%;
    z-index: -1;
    bottom: 0;
    left: 0;
    transform: scaleX(0);
    transition: transform 0.5s;
    transform-origin: left;
}

button:hover::before,
button:hover::after {
    transform: scaleX(1);
}

button:hover {
    color: black;
}

.navbar-icons {
    display: none;
}

@media screen and (max-width : 990px) {

    body {
        white-space: nowrap;
    }

    .navbar-top {
        font-size: 15px;
        z-index: 5;
        justify-content: center;
    }


    .navbar-bottom-right {
        display: none;
    }

    .navbar-bottom {
        z-index: 5;
        width: 100%;
    }

    .navbar-bottom.stick {
        position: fixed;
        width: 100%;
        top: 10%;
        top: 0;
    }

    .navbar-links {
        display: flex;
        flex-direction: column;
        line-height: 50px;
        position: absolute;
        width: 100%;
        overflow: hidden;
        transition: 0.5s;
        z-index: 3;
        top: 100%;
        height: 0rem;
        background-color: #051343;
    }

    .navbar-links.active {
        height: 35.5rem;
    }

    .nav-link {
        width: 100%;
        display: block;
        margin: 10px;
    }

    .navbar-icon {
        position: relative;
        left: 36%;
        top: -23%;
        opacity: 1;
    }

    .navbar-icon i {
        font-size: 33px;
        color: white;
        position: absolute;
        cursor: pointer;
    }

    .navbar-section {
        position: relative;
    }

    .navbar-top-logo {
        position: absolute;
        top: 42%;
        left: 3%;
        color: #314efb;
        z-index: 8;
    }

    .navbar-top-logo.stick {
        position: fixed;
        top: 0;
    }
}

.content {
    text-align: start;
}

.changeBtn {
    background-color: dodgerblue;
    border: 1px solid white;
    padding: 10px;
    cursor: pointer;
}

.icons {
    font-size: 40px;
    cursor: pointer;
}

.logo-link {
    text-decoration: none;
    color: blue;
}