.banner-section {
    background-color: #f5faff;
}

.banner-row {
    display: flex;
    text-align: start;
    align-items: center;
    height: 50rem;
    justify-content: center;
    margin: 0rem 7rem;
}

.banner-col {
    flex-basis: 50%;
}

.banner-col h2 {
    font-size: 35px;
    color: #283b5c;
}

.features {
    display: flex;
    align-items: center;
    line-height: 15px;
    font-weight: bold;
}

.features i {
    font-size: 20px;
    color: rgb(0, 94, 255);
    margin-right: 15px;
}

.banner-col a {
    text-decoration: none;
    color: white;
    background-color: dodgerblue;
    padding: 12px 20px;
    border-radius: 5px;
    position: relative;
    top: 2rem;
}

.banner-img {
    position: relative;
    width: 100%;
    top: 6rem;
}

.banner-img img {
    width: 100%;
    animation: imgMove 5s linear infinite;
}

@keyframes imgMove {
    0% {
        transform: translateY(-8%);
    }

    60% {
        transform: translateY(-5%);
    }

    100% {
        transform: translateY(-8%);
    }
}


@media screen and (max-width: 990px) {
    .banner-section {
        background-color: #f5faff;
        height: 80rem;
    }

    .banner-row {
        display: flex;
        text-align: start;
        align-items: center;
        height: 50rem;
        justify-content: center;
        margin: 0rem 7rem;
        flex-wrap: wrap;
    }

    .banner-col {
        flex-basis: 100%;
        margin: 10px;
        white-space: pre-wrap;
    }

    .banner-col h2 {
        font-size: 25px;
        color: #283b5c;
    }

    .features {
        display: flex;
        align-items: center;
        line-height: 10px;
        font-weight: bold;
    }

    .features i {
        font-size: 20px;
        color: rgb(0, 94, 255);
        margin-right: 15px;
    }

    .banner-col a {
        text-decoration: none;
        color: white;
        background-color: dodgerblue;
        padding: 12px 20px;
        border-radius: 5px;
        position: relative;
        top: 2rem;
    }

    .banner-img {
        position: relative;
        width: 100%;
        top: 4rem;
        height: 40rem;
    }

    .banner-img img {
        width: 100%;
        object-fit: contain;
        height: 100%;
        animation: none;
    }
}


