.info-container {
    height: 10rem;
}

.info-row {
    margin-top: 5rem;
    display: flex;
    justify-content: center;
    padding: 0rem 7rem;
}

.info-col {
    box-shadow: 0px 1px 15px 0px rgba(0, 0, 0, 0.1);
    margin: 15px;
    flex-basis: 33.33%;
    height: 13rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    transition: 0.5s;
}

.info-col:hover {
    transform: translateY(-3%);
}

.info-col h3 {
    font-size: 20px;
    color: #293b5d;
}

.info-col p {
    font-size: 16px;
    color: #293b5d;
}

.info-col i {
    font-size: 22px;
    background-color: #1c42d8;
    color: white;
    border-radius: 50%;
    padding: 10px;
}

.contact-section2 {
    background-color: white;
}

.contact-section2 .services-heading4 {
    background-color: white;
}

.contact-row2 {
    flex-direction: row-reverse;
}

.contact-row2 .contact-img {
    position: relative;
    width: 100%;
    height: 100%;
}

.contact-row2 .contact-img img {
    position: relative;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    animation: none;
}

@media screen and (max-width: 990px) {
    .info-container {
        height: 20rem;
    }

    .info-row {
        margin-top: 5rem;
        display: flex;
        justify-content: center;
        padding: 0rem 7rem;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(300px, auto));
    }

    .info-col {
        box-shadow: 0px 1px 15px 0px rgba(0, 0, 0, 0.1);
        margin: 10px;
        flex-basis: 33.33%;
        height: 13rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        transition: 0.5s;
    }
    .info-col:last-child
    {
        width: calc(96.5% * 2);
    }

