.services-heading {
    height: 10rem;
    padding: 3rem 0rem 0rem 0rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    line-height: 25px;
}

.services-heading p:first-child {
    color: blue;
    font-size: 13px;
    line-height: 0px;
}

.services-heading h1 {
    color: #283b5c;
    font-size: 35px;
}

.services-heading p:last-child {
    width: 50rem;
    white-space: pre-wrap;
}

.review-img {
    position: relative;
    width: 100%;
    height: 5rem;
}

.review-img img {
    width: 100%;
    object-fit: contain;
    position: absolute;
}

.reviews-section {
    background-color: #f5faff;
}

.reviews-col p {
    line-height: 28px;
    font-size: 15px;
    white-space: pre-wrap;
}

.reviews-col h3 {
    font-size: 19px;
    line-height: 5px;
}


.swiper-container {
    width: 480px;
}

@media screen and (max-width: 400px) {
    .swiper-container {
        width: 400px;
    }

}

@media screen and (max-width: 640px) {
    .swiper-container {
        width: 640px;
    }

    .services-heading p:last-child {
        width: 100%;
        white-space: pre-wrap;
    }
}

@media screen and (max-width: 768px) {
    .swiper-container {
        width: 768px;
    }
}

@media screen and (max-width: 1200px) {
    .swiper-container {
        width: 1200px;
    }
}