.about-row {
    height: 22rem !important;
    text-align: center;
    flex-wrap: wrap;
}

.about-row .hero-col {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    align-items: center;
}

.about-row .hero-col h1 {
    font-size: 35px;
    margin-right: 1.3rem;
}

.hero-section-about {
    height: 22rem !important;
}

.about-btn {
    display: none;
}

.path {
    display: flex;
    align-items: center;
}

.path div {
    background-color: blue;
    width: 10px;
    height: 10px;
    margin: 0px 10px;
}

.path-link {
    text-decoration: none;
    color: white;
}

.about-hero-row {
    flex-direction: row-reverse;
}

.about-hero-row .hero-col3 {
    position: relative;
}

.about-hero-row .hero-layer {
    background: transparent;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 50%;
    left: 46%;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translate(-50%, -50%);
}

.hero-layer .video-btn {
    background-color: #1d42d9;
    padding: 10px;
    border-radius: 50%;
    width: 2rem;
    height: 2rem;
    display: flex;
    align-items: center;
    position: relative;
    justify-content: center;
    scale: 1.3;
    cursor: pointer;
}

.video-btn::before {
    content: "";
    position: absolute;
    background-color: #1d42d9;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    animation: scaleBtn 1s linear infinite;
}

@keyframes scaleBtn {
    0% {
        scale: 1;
        opacity: 1;
    }

    100% {
        scale: 1.8;
        opacity: 0;
    }
}

.hero-layer .video-icon {
    position: absolute;
    color: white;
    font-size: 25px;
    cursor: pointer;
}

.video-holder {
    display: none;
}

.video-holder.show
{
    background-color: rgba(0, 0, 0, 0.8);
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.video-holder.hide
{
    display: none;
    animation: showVid 2s linear 1 forwards;
}

@keyframes showVid {
    0% {
    }
    100%{
        transform: translateY(10%);
    }
}


.video-holder i {
    color: white;
    font-size: 28px;
    top: 10%;
    position: absolute;
    right: 17%;
    cursor: pointer;
}