.services-section {
    margin: 0rem 7.5rem;
}

.services-heading {
    height: 10rem;
    margin: 5rem 0rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    line-height: 25px;
}

.services-heading p:first-child {
    color: blue;
    font-size: 13px;
    line-height: 0px;
}

.services-heading h1 {
    color: #283b5c;
    font-size: 36px;
}

.services-heading p:last-child {
    width: 50rem;
}

.services-row {
    display: flex;
    flex-wrap: wrap;
}

.services-col {
    flex-basis: 28.8%;
    margin: 15px;
    box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.058);
    height: 17rem;
    border-radius: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px;
    position: relative;
    overflow: hidden;
    transition: all 0.5s;
}

.services-col:hover {
    color: white;
    transform: translateY(-5%);
}

.services-col::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #041243;
    color: white;
    z-index: -1;
    transform: scaleX(0);
    transform-origin: right;
    transition: transform 0.5s;
}

.services-col::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    color: white;
    z-index: -1;
    background-image: url(../components/images/service-hover.png);
    background-repeat: no-repeat;
    top: 0;
    left: -100%;
    transition: all 0.5s;
}

.services-col:hover::before {
    transform: scaleX(1);
}

.services-col:hover:after {
    left: 0;
}

.service-img {
    width: 100%;
    height: 4rem;
    position: relative;
}

.service-img img {
    width: 100%;
    object-fit: contain;
    height: 100%;
}

.service-content h2 {
    color: #041243;
    font-size: 18px;
}

.services-col:hover h2 {
    color: white;
}

.service-content p {
    font-size: 14px;
}

