.faq-section {
    margin: 0rem 7rem !important;
}

.faq-section {
    margin: 0;
}

.services-heading2 {
    height: 10rem;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    line-height: 25px;
    width: 43rem;
}

.services-heading2 p:first-child {
    color: blue;
    font-size: 13px;
    line-height: 0px;
}

.services-heading2 h1 {
    color: #283b5c;
    font-size: 35px;
    white-space: pre-wrap;
    line-height: 45px;
}

.services-heading2 p:last-child {
    width: 100%;
    white-space: pre-wrap;
    line-height: 30px;
}

.faq-wrapper {
    margin: 10px;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.124);
    height: 4.2rem;
    overflow: hidden;
    transition: all 0.5s;
}

.faq-wrapper.show {
    height: 10rem;
}

.faq-row {
    display: flex;
    margin-bottom: 5rem;
    margin-top: 5rem;
    height: 26rem;
}

.faq-col {
    flex-basis: 50%;
    text-align: start;
    margin: 10px;
}

.faq-col img {
    width: 100%;
}

.faq-top {
    display: flex;
    align-items: center;
    border-bottom: 1px dotted rgba(0, 0, 0, 0.147);
    cursor: pointer;
}

.faq-top h3 {
    color: #283b5c;
}

.faq-top i {
    font-size: 30px;
    font-weight: bold;
    margin: 0px 10px;
}

.faq-Content {
    display: flex;
    align-items: center;
    padding: 10px;

}

.faq-Content p {
    font-size: 15px;
}
