.footer-row {
    display: flex;
    padding: 0rem 6rem;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 35rem;
}

.footer-row::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url(../components/images/footer.png);
    z-index: -1;
    background-position: center;
    background-size: cover;
}

.footer-col:first-child {
    flex-basis: 35%;
    line-height: 30px;
}

.footer-col {
    flex-basis: 24%;
    color: white;
    text-align: start;
    margin: 20px;
}

.footer-col a {
    display: block;
}

.Social-icons {
    display: flex;
}

.Social-icons i {
    background-color: #1d43d8;
    color: white;
    font-size: 15px;
    margin: 10px;
    padding: 10px;
    border-radius: 5px;
}

.footer-input {
    display: flex;
    align-items: center;
}

.footer-input input {
    width: 100%;
    height: 47px;
    border: none;
    outline: none;
}

.footer-input input:focus {
    border: 1px solid red;
}

.footer-input a {
    background-color: #1d43d8;
    color: white;
    text-decoration: none;
    padding: 10px 5px;
    position: relative;
    margin: 0 !important;
}

.footer-input a::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 50%;
    background-color: transparent;
    top: 0;
    left: 0;
    z-index: -1;
    transform: scaleX(0);
    transition: 0.5s;
    transform-origin: left;
}

.footer-input a::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 50%;
    background-color: white;
    top: 0;
    left: 0;
    z-index: -1;
    transform: scaleX(0);
    transition: 0.5s;
    transform-origin: right;
}

.footer-input a:hover::before,
.footer-input a:hover::after {
    transform: scaleX(1);
}

.footer-col a {
    text-decoration: none;
    margin: 10px;
    color: white;
    font-size: 15px;
}

.footer-logo {
    color: white;
}

.footer-logo i {
    margin-right: 5px;
}

.footer-col b {
    font-weight: bold;
    font-size: 23px;
    border-bottom: 2px solid;
    position: relative;
}

.footer-col b::before {
    content: "";
    position: absolute;
    width: 50%;
    height: 6%;
    background-color: rgb(207, 57, 57);
    bottom: -6%;
}

.wrapper {
    display: flex;
    align-items: center;
    line-height: 15.5px;
}

.wrapper i {
    background-color: #1d43d8;
    padding: 8px;
}

.footer-bottom {
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-color: #051242;
    color: white;
}

.footer-bottom a {
    text-decoration: none;
    color: white;
    margin: 10px;
    font-size: 15px;
}

.footer-bottom h3 {
    font-weight: 400;
}

