.Gallery-items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
body
{
    text-align: center;
}

.Gallery-img {
    flex-basis: 22%;
    margin: 5px;
}
.Gallery-img img
{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.filter-list ul
{
    list-style-type: none;
    display: flex;
    justify-content: center;
    cursor: pointer;
}
.filter-list li
{
    margin: 10px;
    padding: 6px;
    background-color: dodgerblue;
    width: 6rem;
    text-align: center;
}