.services-heading4 {
    height: 10rem;
    margin: 5rem auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    line-height: 25px;
    width: 43rem;
}

.services-heading4 p:first-child {
    color: blue;
    font-size: 13px;
    line-height: 0px;
}

.services-heading4 h1 {
    color: #283b5c;
    font-size: 35px;
    white-space: pre-wrap;
    line-height: 45px;
}

.case-row {
    display: flex;
    flex-wrap: wrap;
    padding: 0rem 7rem;
}

.case-col {
    flex-basis: 30.6%;
    margin: 15px;
    border-radius: 5px;
    overflow: hidden;
    text-align: start;
    box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.1);
    transition: 0.5s;
}

.case-img {
    position: relative;
    width: 100%;
    cursor: pointer;
}

.case-img img {
    width: 100%;
    object-fit: contain;
}

.case-content {
    padding: 15px;
}

.case-content a {
    font-weight: bold;
    cursor: pointer;
    text-decoration: none;
    color: #283b5c;
    transition: 0.5s;
    font-size: 15px;
}

.case-content a:hover {
    letter-spacing: 1px;
}

.case-content h3 {
    color: #283b5c;
    cursor: pointer;
}

.case-col:hover h3 {
    color: rgba(0, 0, 255, 0.676);
}

.case-col:hover {
    transform: translateY(-1%);
}

.view-more {
    text-align: center;
    margin: 3rem auto;
}

.view-more span {
    color: blue;
    cursor: pointer;
}

