.pricing-row {
    display: flex;
    justify-content: center;
    padding: 0rem 6rem;
}

.pricing-col {
    flex-basis: 33.33%;
    margin: 15px;
    box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.1);
    border-top-left-radius: 10%;
    border-bottom-right-radius: 10%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    position: relative;
    overflow: hidden;
    color: #293b5d;
    transition: 0.5s;
    padding-bottom: 3rem;
}

.pricing-col:hover
{
    transform: translateY(-1%);
}

.price {
    background-color: white;
    box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.1);
    margin: 0rem 6rem;
    height: 7rem;
    width: 7rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    font-size: 27px;
}

.feature-col {
    text-align: start;
    padding: 1rem 5rem;
    color: #7f7e9b;
}

.feature {
    display: flex;
    align-items: center;
}

.feature i {
    color: dodgerblue;
    padding-right: 10px;
}

.feature p {
    font-weight: bold;
    font-size: 13.5px;
    line-height: 10px;
}

.popular {
    background-color: red;
    color: white;
    height: 10rem;
    width: 10rem;
    display: flex;
    justify-content: center;
    align-items: end;
    position: absolute;
    top: -16%;
    right: -25%;
    transform: rotate(44deg);
    font-weight: bold;
}

.price-btn {
    background-color: #1c42d8;
    color: white;
    padding: 14px 28px;
    border-radius: 5px;
    position: relative;
    z-index: 1;
    overflow: hidden;
    cursor: pointer;
}

.price-btn::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 50%;
    top: 0;
    left: 0;
    z-index: -1;
    transform: scaleX(0);
    transform-origin: bottom left;
    transition: transform 0.5s;
    background-color: #051242;
}


.price-btn::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 50%;
    bottom: 0;
    left: 0;
    z-index: -1;
    background-color: #051242;
    transform: scaleX(0);
    transform-origin: top right;
    transition: transform 0.5s;
}

.price-btn:hover:before,
.price-btn:hover:after {
    transform: scale(1);
}

