#App {
    height: 100%;
}

html,
body {
    position: relative;
    height: 100%;
}

body {
    background: #fff;
    font-family: 'poppins';
    font-size: 14px;
    color: #000;
    margin: 0;
    padding: 0;
}

.swiper {
    width: 82%;
    height: 100%;
}

.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.team-col {
    line-height: 10px;
    position: relative;
}

.team-col h3 {
    color: #041342;
    font-size: 20.5px;
    transition: all 0.5s;
}

.team-col p {
    font-size: 15.5px;
}

.team-content {
    transition: all 0.5s;
}

.swiper-container {
    width: 480px;
}

@media screen and (max-width: 400px) {
    .swiper-container {
        width: 400px;
    }
}

@media screen and (max-width: 640px) {
    .swiper-container {
        width: 640px;
    }
}

@media screen and (max-width: 768px) {
    .swiper-container {
        width: 768px;
    }
}

@media screen and (max-width: 1100px) {
    .swiper-container {
        width: 1100px;
    }
}

.layer {
    background-color: #05133c;
    position: absolute;
    width: 100%;
    height: 0%;
    bottom: -15%;
    transition: all 0.5s;
    z-index: -1;
}

.team-col:hover .layer {
    height: 50%;
}

.icons-wrapper {
    position: relative;
    overflow: hidden;
    height: 3rem;
}

.social-icons {
    position: absolute;
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
    transition: all 0.5s;
    bottom: -100%;
}
.team-col:hover .social-icons
{
    bottom: 0;
}

.team-col:hover .team-content,
.team-col:hover .team-content h3 {
    color: white;
}

.social-icons i {
    background-color: white;
    color: #071f6a;
    margin: 8px;
    padding: 5px;
    border-radius: 50%;
    width: 1.5rem;
    height: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.5s;
    cursor: pointer;
}

.social-icons i:hover {
    color: white;
    background-color: #1c43b7;
}