.team-g-row {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
    padding: 0rem 7rem;
}

.team-g-col {
    flex-basis: 32%;
    box-shadow: 0px 0.5px 10px 0px rgba(0, 0, 0, 0.1);
    transition: 0.5s;
    overflow: hidden;
}

.team-g-img {
    position: relative;
    width: 100%;
    height: 30rem;
}

.team-g-img img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    z-index: -1;
}

.team-g-content {
    background-color: white;
    height: 5rem;
    position: relative;
    bottom: 5rem;
    display: flex;
    flex-direction: column;
    line-height: 5px;
    z-index: 5;
}

.team-g-content h3 {
    font-size: 20px;
    color: #131f4c;
}

.team-g-content p {
    font-size: 16px;
}

.team-g-content i {
    background-color: #131f4c;
    margin: 5px;
    font-size: 20px;
    color: white;
    border-radius: 50%;
    padding: 6px;
    cursor: pointer;
    transition: 0.5s;
}

.team-g-social i:hover {
    transform: translateY(-5%);
}


.team-g-col:hover {
    transform: translateY(-1%);
}


