.hero-section {
    height: 40rem;
    z-index: -10;
    background-color: #051343;

}

.hero-row {
    display: flex;
    color: white;
    align-items: center;
    justify-content: center;
    height: 38rem;
    position: relative;
    margin: 0rem 6rem;
}

.hero-col {
    flex-basis: 50%;
}

.hero-img {
    position: relative;
    width: 100%;
}

.hero-img img {
    width: 100%;
    height: 100%;
}

.hero-col h1 {
    font-size: 50px;
    text-align: start;
    padding-left: 1.5rem;
    white-space: pre-wrap;
}

.hero-col:nth-child(1) {
    width: 5rem;
}

.buttons .button {
    background-color: #1d43d8;
    border: 1px solid white;
    padding: 16px 19px;
    border-radius: 5px;
    margin: 7px;
    color: white;
    font-weight: bold;
}

button {
    width: 8rem;
    border-radius: 5px;
    cursor: pointer;
    border: none;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    z-index: 2;
    font-weight: bold;
    overflow: hidden;
    background-color: dodgerblue;
    transition: 0.5s;
}

.buttons button:hover {
    color: black;
}

button::before {
    content: "";
    position: absolute;
    background-color: white;
    height: 50%;
    width: 100%;
    z-index: -1;
    top: 0;
    left: 0;
    transition: transform 0.5s;
    transform: scaleX(0);
    transform-origin: right;
}

.buttons {
    display: flex;
    padding-left: 1rem;
    margin-top: 1rem;
}

.hero-col .heroimg1 {
    animation: scale 10s linear infinite;
}

@keyframes scale {

    0%,
    100% {
        scale: calc(0.9 + 0.2);
    }

    60%,
    60% {
        scale: calc(0.9 + 0.2);
    }

    100%,
    0% {
        scale: calc(1);
    }
}

.anime1 img {
    position: absolute;
    left: 5%;
    top: 10%;
    transform: scale(1);
    animation: anime1 6s linear infinite;
}

@keyframes anime1 {

    0%,
    100% {
        transform: scale(1);
    }

    60%,
    60% {
        transform: scale(1);
    }

    100%,
    0% {
        transform: scale(1.1);
    }
}

.anime2 {
    position: absolute;
    left: 0;
    font-size: 50px;
    color: #e3325b;
    animation: anime2 10s linear infinite;
}

@keyframes anime2 {
    0% {
        transform: rotate();
    }

    100% {
        transform: rotate(340deg);
    }
}

.anime3 {
    position: absolute;
    left: 50%;
    top: 10%;
    font-size: 50px;
    color: #e3325b;
    animation: anime3 10s linear infinite;
}

@keyframes anime3 {
    0% {
        transform: rotate();
    }

    100% {
        transform: rotate(340deg);
    }
}

.anime4 {
    position: absolute;
    right: 10%;
    top: 10%;
    font-size: 50px;
    animation: anime4 15s linear infinite;
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    padding: 4px;
    border: 4px solid #e3325b;
    opacity: 0.7;
}

@keyframes anime4 {
    0% {
        top: 10%;
    }

    20% {
        top: 20%;
    }

    30% {
        top: 20%;
        right: 20%;
    }

    40% {
        top: 10%;
    }
}

.anime5 {
    position: absolute;
    right: 10%;
    bottom: 30%;
    font-size: 45px;
    animation: anime5 55s linear infinite;
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    padding: 4px;
    opacity: 1;
    color: #003675;
}

@keyframes anime5 {
    0% {
        bottom: 30%;
    }

    20% {
        bottom: 40%;
    }

    30% {
        right: 5%;
    }

}

.anime6 {
    position: absolute;
    left: 30%;
    bottom: 0;
    font-size: 40px;
    color: #e3325b;
    animation: anime6 10s linear infinite;
}

@keyframes anime6 {
    0% {
        transform: rotate();
    }

    100% {
        transform: rotate(340deg);
    }
}


@media screen and (max-width: 990px) {
    .hero-section {
        z-index: -5;
        height: 55rem;
    }

    .hero-row {
        width: 100%;
        flex-wrap: wrap;
        margin: 0;
    }

    .hero-col {
        flex-basis: 100%;
        padding: 0;
        margin: 0;
    }

    .hero-col img {
        width: 100%;
    }

    .hero-col .heroimg1 {
        animation: scale 10s linear infinite;
    }

    @keyframes scale {

        0%,
        100% {
            transform: scale(0.8);
        }

        60%,
        60% {
            transform: scale(0.8);
        }

        100%,
        0% {
            transform: scale(0.9);
        }
    }

    .hero-col h1 {
        font-size: 40px;
        text-align: center;
        padding-left: 0;
    }

    .hero-col p {
        font-size: 15px;
        white-space: pre-wrap;
    }

    .hero-col:nth-child(1) {
        padding-left: 1rem;
    }

    .buttons {
        display: flex;
        padding-right: 2rem;
        margin-top: 1rem;
        justify-content: center;
    }

    /* Second Hero Styles */



    .company-section {
        z-index: 1;
        height: 45rem;
    }

    .hero-section {
        z-index: 2;
    }

    .animation {
        display: none;
    }

    .hero-row2 {
        display: flex;
        color: black;
        align-items: center;
        justify-content: center;
        height: 60rem !important;
        position: relative;
        background-color: #f5fbfe;
        z-index: -1;
        text-align: start;
        padding-right: 3rem;
        z-index: 3;
        flex-wrap: wrap;
        padding: 0rem !important;
    }

    .hero-row2 .btn1 {
        display: none;
    }

    .hero-row2 .sml-p {
        color: blue;
    }

    .hero-row2 h1 {
        font-size: 30px !important;
        padding-left: 0 !important;
        text-align: start;
    }

    .hero-row2 .buttons {
        margin: 0;
        padding: 0;
        justify-content: start;
    }

    .hero-row2 .heroimg2 {
        width: 100% !important;
        object-fit: contain;
        height: 100%;
    }

    .hero-row2 .hero-col {
        margin: 15px;
        flex-basis: 100%;
        text-align: start;
        margin: 0rem 6rem !important;
    }

    .hero-row2 .hero-img2 {
        position: relative;
        height: 27rem;
        width: 10rem;
    }

    .hero-row2 .hero-img2::before {
        background-color: transparent !important;
        content: "";
        width: 50%;
        position: absolute;
        height: 50%;
        top: -3%;
        left: -6%;
        z-index: -1;
    }

    .hero-row2 .hero-img2::after {
        background-color: transparent !important;
        content: "";
        width: 50%;
        position: absolute;
        height: 50%;
        bottom: -3%;
        right: 3%;
        z-index: -1;
    }

    .hero-row2 .btn2 {
        background-color: #1d42d9;
        border: 1px solid white;
        padding: 16px 19px;
        border-radius: 5px;
        margin: 7px;
        color: white;
        font-weight: bold;
        position: relative;
    }

    .hero-row2 .btn2::before {
        content: "";
        background-color: #041243;
        width: 100%;
        height: 50%;
        position: absolute;
        top: 0;
    }

    .hero-row2 .btn2::after {
        background-color: #041243;
    }

    .hero-row2 .btn2:hover {
        color: white;
    }
}


